@import url(https://fonts.googleapis.com/css2?family=Baloo+Chettan+2:wght@600;800&display=swap);
*{margin:0;box-sizing:border-box;font-family:"Open Sans",sans-serif}li,ul li{margin:0;padding:0;text-indent:0;list-style-type:0}a{text-decoration:none;outline:none}a:link{text-decoration:none}
.site-container{position:absolute;width:100%;height:100%}.site-container .main-content{position:relative;display:flex;flex-direction:column;width:100%;max-width:1080px;height:auto;left:50%;transform:translateX(-50%);padding-left:20px;padding-right:20px}
ul{list-style:none;padding-top:.3em}ul li{display:inline;font-size:.7em;font-weight:bold;padding-left:1em}ul li:hover{cursor:pointer;color:#f956c9}.navbar{position:fixed;width:100%;max-width:1120px;height:110px;padding-left:20px;padding-right:20px;left:50%;transform:translateX(-50%);z-index:9999}.navbar .top-link-items{position:relative;display:flex;justify-content:flex-end;width:100%;height:40px;background-color:#fff}.navbar .navbar-content{position:relative;display:flex;justify-content:space-between;width:100%;height:calc(63%);background-color:rgba(255,255,255,.95)}.navbar .navbar-content .mobile-menu-container{display:none}.navbar .navbar-content .button-group{position:relative;display:flex;width:530px;height:100%}.navbar .navbar-content .button-group .brand-title{position:relative;display:grid;width:230px;height:55px;place-content:center;padding-top:.7em}.navbar .navbar-content .button-group .brand-title span{font-family:"Baloo Chettan 2",cursive;font-size:2em;font-weight:800;color:#00d3ff}.navbar .navbar-content .button-group .brand-title span:hover{color:#f956c9}.navbar .navbar-content .button-group .main-menu-container{position:relative;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr;width:300px;height:calc(85%);place-content:center;padding-top:1.2em;padding-left:1em}.navbar .navbar-content .button-group .main-menu-container .main-menu-button{position:relative;width:130px;height:inherit;background-color:#f2f2f2;text-align:center;padding-top:.2rem;color:#000}.navbar .navbar-content .button-group .main-menu-container .main-menu-button #shop-menu{padding-left:1em}.navbar .navbar-content .button-group .main-menu-container .main-menu-button span{font-weight:700;font-size:.9rem}.navbar .navbar-content .button-group .main-menu-container .main-menu-button:nth-child(2):hover{cursor:pointer;background-color:#000;color:#fff}.navbar .navbar-content .button-group .main-menu-container .active{background-color:#000;color:#fff}.navbar .navbar-content .icons-container{position:relative;display:flex;width:200px;height:calc(63%);background-color:orange;margin-top:1.3em}.gg-menu-left-alt{position:relative;display:inline-block;width:16px;height:14px;transform:scale(1);transform:scale(var(--ggs, 1));color:#fff}.gg-menu-left-alt::after,.gg-menu-left-alt::before{content:"";position:absolute;box-sizing:border-box;display:block;height:2px;border-radius:4px;background:currentColor;top:1px}.gg-menu-left-alt::before{width:14px;box-shadow:0 12px 0;top:1px}.gg-menu-left-alt::after{width:12px;top:7px}@media screen and (max-width: 1024px){.navbar{height:102px}}@media screen and (max-width: 768px){.navbar{position:fixed;height:102px}.navbar .navbar-content{height:calc(44%)}.navbar .navbar-content .button-group .brand-title{left:-1em}.navbar .navbar-content .button-group .main-menu-container{display:none}.navbar .navbar-content .icons-container{margin-top:1.1em}.navbar .navbar-content .mobile-menu-container{display:inline-block;padding:5px}.navbar .navbar-content .mobile-menu-container #shop-menu{color:#000;margin-top:1em}.navbar .navbar-content .mobile-menu-container .gg-menu-left-alt{position:relative;display:inline-block;width:16px;height:16px;transform:scale(1);transform:scale(var(--ggs, 1));color:#fff}.navbar .navbar-content .mobile-menu-container .gg-menu-left-alt::before{width:22px;height:3px;box-shadow:0 15px 0;top:1px}.navbar .navbar-content .mobile-menu-container .gg-menu-left-alt::after{height:3px;width:20px;top:9px}}
.resize-container{position:relative;width:100%;margin-top:110px;margin-bottom:40px}.resize-container .hero-slideshow-container{position:relative;width:100%;background-color:red}.resize-container .hero-slideshow-container .slideshow-img-box{background-size:cover;width:100%}@media screen and (max-width: 768px){.hero-slideshow-container{position:relative;width:100%;height:0;padding-bottom:100%}}
