.products-grid-container{position:relative;display:grid;grid-template-columns:repeat(auto-fit, minmax(240px, 1fr));grid-column-gap:24px;-webkit-column-gap:24px;column-gap:24px;width:100%;height:auto;margin-top:2em}.products-grid-container .product-item{position:relative;min-width:20px;margin-bottom:24px}.products-grid-container .product-item .product-img-container{position:relative;display:block;width:100%;height:0;padding-bottom:100%}.products-grid-container .product-item .product-img-container .fit-img{background-size:cover;width:100%}.products-grid-container .product-item .description-container{position:relative;display:flex;flex-direction:column;width:100%;height:auto;text-align:center}.products-grid-container .product-item .description-container .brand{position:relative;display:block;color:#c2c2c2}.products-grid-container .product-item .description-container .product-title{position:relative;display:block;color:#000;font-weight:500;font-size:.9rem}.products-grid-container .product-item .description-container .board-size{display:block;color:#c2c2c2}.products-grid-container .product-item .description-container .product-price{display:block;color:#c2c2c2}
.main-footer-container{position:relative;width:100%;height:200px;margin-top:130px;margin-bottom:500px}.main-footer-container .upper-footer-container{position:absolute;width:100%;height:250px;background-color:#00d3ff}.main-footer-container .upper-footer-container .upper-footer-section{position:relative;display:flex;justify-content:space-around;width:100%;height:128px;top:50%;transform:translateY(-50%)}.main-footer-container .upper-footer-container .upper-footer-section .mailing-list-signup-container{position:relative;display:flex;flex-direction:column;width:328px;height:inherit;text-align:center}.main-footer-container .upper-footer-container .upper-footer-section .mailing-list-signup-container .subtext{position:relative;margin:0 auto;line-height:20px;padding-top:.6rem;font-size:13px;font-weight:bold}.main-footer-container .upper-footer-container .upper-footer-section .mailing-list-form{position:relative;margin-top:auto}.main-footer-container .upper-footer-container .upper-footer-section .mailing-list-form .mailing-list-email{padding:8px 12px 10px;line-height:18px;font-weight:bold;border:none}.main-footer-container .upper-footer-container .upper-footer-section .mailing-list-form .mailing-list-submit{padding-bottom:10px;line-height:18px;font-size:13px;width:124px;background-color:#000;color:#fff;font-weight:bold;padding-top:8px;border:none}.main-footer-container .upper-footer-container .upper-footer-section .social-container{position:relative;display:flex;flex-direction:column;text-align:center;width:328px;height:inherit;padding-top:20px}.main-footer-container .upper-footer-container .upper-footer-section .social-container .social-items{position:relative;display:grid;grid-template-columns:repeat(5, 1fr);width:100%;height:auto;padding-top:1em}.main-footer-container .upper-footer-container .upper-footer-section .social-container .social-items .social-item{position:relative;display:inline-block;width:36px;height:36px;border-radius:100%;background-color:#fff;margin-right:1em}.main-footer-container .headline{position:relative;font-weight:bold;font-size:1em;line-height:20px}.main-footer-container .cloud-border-container{position:relative;display:flex;flex-wrap:none;width:100%;height:1em;top:1em;overflow:visible;margin:0 auto}.main-footer-container .cloud-border-container .cloud-circle{position:relative;width:1.5em;height:1.5em;border-radius:50%;background-color:#fff;z-index:10}.main-footer-container .bottom{top:calc(100% + 0.5em)}.main-footer-container .bottom .cloud-circle{background-color:#fff}@media screen and (max-width: 768px){.upper-footer-container{display:grid;place-content:center}.upper-footer-container .upper-footer-section{flex-direction:column}.upper-footer-container .upper-footer-section .mailing-list-signup-container{padding-bottom:2em}}
