.site-container {
  position: absolute;
  width: 100%;
  height: 100%;
  // background-color: red;

  .main-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1080px;
    height: auto;
    // background-color: goldenrod;
    left: 50%;
    transform: translateX(-50%);
    padding-left: 20px;
    padding-right: 20px;
  }
}
